<h3 id="cache" tabindex="-1"><a class="header-anchor" href="#cache" aria-hidden="true">#</a> cache</h3>
<p>• <code>Readonly</code> <strong>cache</strong>: <code>string</code></p>
<p>Directory for non-essential data files.
Example locations (with the default <code>nodejs</code> suffix):</p>
<ul>
<li>macOS: <code>~/Library/Caches/MyApp-nodejs</code></li>
<li>Windows: <code>%LOCALAPPDATA%\MyApp-nodejs\Cache</code> (for example, <code>C:\Users\USERNAME\AppData\Local\MyApp-nodejs\Cache</code>)</li>
<li>Linux: <code>~/.cache/MyApp-nodejs</code> (or <code>$XDG_CACHE_HOME/MyApp-nodejs</code>)</li>
</ul>
