<p>• <code>Readonly</code> <strong>config</strong>: <code>string</code></p>
<p>Directory for data files.
Example locations (with the default <code>nodejs</code> suffix):</p>
<ul>
<li>macOS: <code>~/Library/Preferences/MyApp-nodejs</code></li>
<li>Windows: <code>%APPDATA%\MyApp-nodejs\Config</code> (for example, <code>C:\Users\USERNAME\AppData\Roaming\MyApp-nodejs\Config</code>)</li>
<li>Linux: <code>~/.config/MyApp-nodejs</code> (or <code>$XDG_CONFIG_HOME/MyApp-nodejs</code>)</li>
</ul>
